<template>
	<view>
		<searchArea ref='searcharea' :auto-datas="autoDatas" ></searchArea>
		<!-- 搜索历史区 -->
		<view class="search-history" v-show="update" >
			<view class="history-title">
				<text>历史搜索</text>
			</view>
			<view class="history-content">
				<view class="border" v-for="(item,index) in historyDatas" :key="item.id" @click="searchHistory(item.keyword)">
					<text class="clamp">{{item.keyword}}</text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import searchArea from '@/components/search-area/search-area.vue'
	export default {
		components: {
			searchArea
		},
		data() {
			return {
				historyDatas: [],
				historyNum: 0,
				update: false,
				autoDatas:[]
			}
		},
		onLoad() {
			this.getJson();
			// console.log(this.autoDatas)
		},
		methods: {
			changeHistory(res) {
				console.log(res)
			},
			searchHistory(key){
				console.log(key);
			},
			getJson() {
				// 先从后台拿数据
				uni.request({
					// #ifdef H5
					url: "/static/fake.json",
					// #endif
					// #ifdef MP-WEIXIN
					url: "https://xcxcs.obs.cn-east-3.myhuaweicloud.com/fake.json",
					// #endif
					method: "get",
					dataType: "JSON",
					// #ifdef H5
					success: res => {
						this.autoDatas = res.data.autoDatas;
						// console.log(this.autoDatas);
					},
					// #endif
					// #ifdef MP-WEIXIN
					success: res => {
						// console.log(res.data);
						let data = JSON.parse(res.data)
						this.autoDatas = data.autoDatas;
					}
					// #endif
				});
			},
		},
		mounted() {
			uni.getStorage({
				key: 'historyDatas',
				success:(res) =>{
					this.historyDatas = res.data;
				}
			});
			// uni.getStorage({
			// 	key: 'historyNum',
			// 	success: (res) => {
			// 		this.historyNum = res.data;
			// 	}
			// });
		},
		watch: {
			historyDatas: {
				handler(newVal, oldVal) {
					this.update = false;
					this.update = true;
				}
			}
		}
	}
</script>

<style lang="scss">
	.search-history {
		padding: 0 30rpx;

		.history-title {
			font-size: 24rpx;
			line-height: 1.5;
			color: #020202;
		}

		.history-content {
			display: flex;
			flex-wrap: wrap;
			justify-content: flex-start;
			align-items: center;
			margin-top: 20rpx;

			.border {
				padding: 0 30rpx;
				height: 60rpx;
				line-height: 60rpx;
				border-radius: 30rpx;
				background-color: #EDEBEB;
				margin-right: 20rpx;
				margin-bottom: 20rpx;

				.clamp {
					font-size: 24rpx;
					color: #595858;
				}
			}
		}
	}
</style>
